@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  color: white;
  padding-top: 50px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-family: "Roboto", sans-serif;
}
.App:before {
  content: " ";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  z-index: 1;
  opacity: 0.8;
}

.header {
  z-index: 2;
}

.App.day.sunny {
  background-color: #3c7ab5;
}

.App.day.sunny:before {
  background: url("/WEATHER-INSIGHT/static/media/—Pngtree—sun rays_72745.9df89ae3.png") no-repeat center
    bottom/cover;
  height: 122%;
}
.App.day.partially-cloudy {
  background-color: #3c7ab5;
}

.App.day.partially-cloudy:before {
  background: url("https://www.transparentpng.com/thumb/sky/29B121-new-the-cooling-shelf-products-exquisa.png")
    no-repeat center center/cover;
}
.App.day.cloudy {
  background-color: #4c5d77;
}

.App.day.cloudy:before {
  background: url("/WEATHER-INSIGHT/static/media/—Pngtree—blue sky blue white clouds_5438056.3764bb17.png")
    no-repeat center center/cover;
}

.App.day.rainy {
  background-color: #3c6891;
}

.App.day.rainy:before {
  background: url("/WEATHER-INSIGHT/static/media/—Pngtree—rain rain effect_5990884.65dceada.png") no-repeat
    center center/cover;
}

.App.night {
  background-color: #212a44;
}

.App.night:before {
  opacity: 0.3;
  background: url("/WEATHER-INSIGHT/static/media/—Pngtree—blue creative texture starry sky_5370563.70299d77.png")
    no-repeat center center/cover;
}

.App.night.cloudy {
  background-color: #212a44;
}

.App.night.cloudy:before {
  background: url("/WEATHER-INSIGHT/static/media/—Pngtree—realist cloud vector_8582.8f58cc5f.png") no-repeat
    center center/cover;
}

.App.night.rainy {
  background-color: #212a44;
}

.App.night.rainy:before {
  background: url("/WEATHER-INSIGHT/static/media/—Pngtree—rain rain effect_5990884.65dceada.png") no-repeat
    center center/cover;
}

input {
  height: 45px;
  width: 350px;
  background-color: rgba(221, 219, 219, 0.315);
  border: none;
  border-radius: 5px;
  padding: 8px;
  font-size: large;
}

input::-webkit-input-placeholder {
  color: rgb(202, 199, 199);
}

input:-ms-input-placeholder {
  color: rgb(202, 199, 199);
}

input::placeholder {
  color: rgb(202, 199, 199);
}

input:focus {
  outline: none;
}

.card {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  z-index: 2;
}

.card .current {
  width: -webkit-max-content;
  width: max-content;
  height: -webkit-max-content;
  height: max-content;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.x-large-font {
  font-size: x-large;
}
.xx-large-font {
  font-size: xx-large;
}

.card .current .temp {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 80%;
  font-size: 80px;
}

.card .current .temp img {
  width: 120px;
}

.card .current .condition-text {
  text-transform: capitalize;
}

.card .current .updated-on {
  font-size: smaller;
  margin-top: 10px;
  font-weight: bold;
}

.card .current .faltu-cheeza {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 15px;
  font-size: small;
  grid-row-gap: 8px;
  row-gap: 8px;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}
h2 {
  margin-top: 10px;
  font-size: xx-large;
  font-weight: normal;
  width: 90%;
  padding: 0 25px;
}
.hourly-update {
  margin-top: -20px;
  width: 90%;
  padding: 8px;
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.hourly-cards {
  width: 100%;
  display: flex;
  overflow-x: auto;
  /* border-top: 2px solid black;
  border-bottom: 2px solid black; */
  padding: 25px 0;
  scroll-behavior: smooth;
}

.hourly-cards::-webkit-scrollbar {
  display: none;
}

.hourly-card {
  flex: 1 0 11%;
  margin: 0 10px;
}
.details {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 12px;
  background-color: rgba(224, 220, 220, 0.212);
  border-radius: 8px;
}
.hourly-card img {
  width: 80px;
  /* border: 1px solid black; */
}

.hourly-card .temp {
  align-self: center;
}
.hourly-card .condition-text {
  margin-bottom: 12px;
  align-self: center;
}

.small-font {
  font-size: x-small;
}

.time {
  margin-top: 6px;
  text-align: center;
  font-weight: bold;
}

.hourly-update button {
  background-color: rgba(197, 195, 195, 0.274);
  border: none;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  margin-left: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(212, 209, 209, 0.644);
  cursor: pointer;
}

